import { setMoveLogo, setWireframe } from './script.js';
import { nav } from './navigation.js';
import { gsap } from 'gsap';
import { marked } from 'marked';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
gsap.registerPlugin(ScrollTrigger);

function findMenu(path, menuArray = nav, parent = null) {
    for (const item of menuArray) {
        if (!item.display) {
            removeFixedClass();
            setWireframe(false);
        }
        if (path === item.history) {

            if (item.menu) {
                return item.menu;
            } else if (item.display) {
                if (item.display === 'grid') {
                    addFixedClass();
                    setWireframe(true);
                    setGridContent(item.content);
                    return menuArray;
                }
                else if (item.display === 'blog') {
                    addFixedClass();
                    setWireframe(true);
                    setContent(item.content);
                    return menuArray;
                }
                else {
                    console.error('Unknown display type: ' + item.display);
                }

            }
        }
        else if (item.menu && path.startsWith(item.history)) {
            if (path.indexOf('projects/') === -1) {
                const foundMenu = findMenu(path, item.menu, menuArray);
                if (foundMenu) {
                    return foundMenu;
                }
            } else {
                const language = path.split('/')[1];
                const project = path.split('/')[3];
                addFixedClass();
                setWireframe(true);
                setMarkdownContent(`${project}-${language}`);
                return item.menu;
            }
        }
    }
    return nav;
}

function setGridContent(content) {
    const page = document.getElementById('page');
    page.innerHTML = '';
    import(`./content/${content}.js`).then(module => {
        const pageContent = module.page;
        const insideContent = document.createElement('div');
        insideContent.classList.add('grid-content');
        page.appendChild(insideContent);

        pageContent.content.forEach(section => {
            const container = document.createElement('div');
            container.classList.add('grid-item');

            const image = document.createElement('div');
            image.classList.add('grid-image');

            const title = document.createElement('h2');
            title.textContent = section.title;
            const description = document.createElement('p');
            description.textContent = section.description;

            const txtContainer = document.createElement('div');
            txtContainer.classList.add('grid-text');
            txtContainer.appendChild(title);
            txtContainer.appendChild(description);

            if (section.link) {
                const linkContainer = document.createElement('div');
                linkContainer.classList.add('grid-link');
                if (window.innerWidth > 800)
                    container.appendChild(image);

                container.appendChild(linkContainer);
                container.appendChild(txtContainer);
                insideContent.appendChild(container);

                const btn = new SVGButton(linkContainer, '+', () => {
                    history.pushState({}, '', section.link);
                });
            } else {
                if (window.innerWidth > 800)
                    container.appendChild(image);
                container.appendChild(txtContainer);
                insideContent.appendChild(container);
            }

            // if window size is greater than 800px, add the svg grid image
            if (window.innerWidth > 800) {
                const svg = new SVGGridImage(image, section.image, section.num ? section.num : 7);
            }
        });

        // gsap.fromTo(insideContent.children, { autoAlpha: 0 }, { autoAlpha: 1, stagger: 0.25 });
    })
}

function setContent(content) {
    const page = document.getElementById('page');
    page.innerHTML = '';
    import(`./content/${content}.js`).then(module => {
        const pageContent = module.page;
        const insideContent = document.createElement('div');
        insideContent.classList.add('inside-content');
        const title = document.createElement('h1');
        title.textContent = pageContent.title;
        insideContent.appendChild(title);

        pageContent.content.forEach(section => {
            const description = document.createElement('p');
            description.textContent = section.description;
            insideContent.appendChild(description);
        });

        page.appendChild(insideContent);
        gsap.fromTo(insideContent.children, { autoAlpha: 0, y: 20 }, { autoAlpha: 1, y: 0, stagger: 0.25 });
    });
}

function setMarkdownContent(markdownFile) {
    const page = document.getElementById('page');
    page.innerHTML = '';

    // Import the markdown file as text
    fetch(`/content/${markdownFile}.md`)
        .then(response => {
            return response.text();
        })
        .then(markdown => {
            // Create container
            const insideContent = document.createElement('div');
            insideContent.classList.add('inside-content');
            // Convert markdown to HTML
            const html = marked.parse(markdown);
            insideContent.innerHTML = html;

            // for each a tag, add a click event to prevent default behavior
            insideContent.querySelectorAll('a').forEach(a => {
                a.addEventListener('click', (e) => {
                    e.preventDefault();
                    const href = a.getAttribute('href');
                    if (href.startsWith('http')) {
                        window.open(href, '_blank');
                    } else {
                        history.pushState({}, '', href);
                    }
                });
            });

            // Add to page
            page.appendChild(insideContent);

            // Animate content
            gsap.fromTo(
                insideContent.children,
                {
                    autoAlpha: 0,
                    y: 20
                },
                {
                    autoAlpha: 1,
                    y: 0,
                    stagger: 0.15
                }
            );

            // Array.from(insideContent.children).forEach(child => {
            //     gsap.fromTo(child, 
            //         {
            //             autoAlpha: 0,
            //             y: 50
            //         },
            //         {
            //             scrollTrigger: {
            //                 trigger: child,
            //                 start: "top bottom",
            //                 toggleActions: "play none none reverse"
            //             },
            //             autoAlpha: 1,
            //             y: 0,
            //             duration: 0.8,
            //             ease: "power2.out"
            //         }
            //     );
            // });
        })
        .catch(error => console.error('Error loading markdown:', error));
}

function addFixedClass() {
    const content = document.getElementById('content');
    content.classList.add('fixed');
    const page = document.getElementById('page');
    page.innerHTML = '';
    page.classList.add('show');
}

function removeFixedClass() {
    const content = document.getElementById('content');
    content.classList.remove('fixed');
    const page = document.getElementById('page');
    page.innerHTML = '';
    page.classList.remove('show');
}

function updateMenu(menu) {
    const menuContainer = document.getElementById('content');
    menuContainer.innerHTML = '';

    if (menu) {
        menu.forEach(menuItem => {
            const btn = new SVGButton(menuContainer, menuItem.label, () => {
                onClick(menuItem);
            });
            const path = window.location.pathname;
            if (path.indexOf(menuItem.history) !== -1) {
                btn.addClass('selected');
            }
        });

    }
}


class SVGButton {
    constructor(container, text, onClick) {
        this.width = 150;
        this.element = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
        this.element.setAttribute('width', this.width);
        this.element.setAttribute('height', 50);
        this.element.setAttribute('viewBox', '0 0 ' + this.width + ' 50');
        this.element.setAttribute('xmlns', 'http://www.w3.org/2000/svg');
        this.element.setAttribute('xmlns:xlink', 'http://www.w3.org/1999/xlink');
        this.element.setAttribute('class', 'svg-button');

        container.appendChild(this.element);

        this.element.addEventListener('click', onClick);

        this.setText(text);
        this.addClass('svg-button');
    }

    updateWidth(width) {
        this.width = width;
        this.element.setAttribute('width', width);
        this.element.setAttribute('viewBox', '0 0 ' + width + ' 50');
    }

    setText(text) {
        const textElement = document.createElementNS('http://www.w3.org/2000/svg', 'text');
        textElement.setAttribute('x', 50);
        textElement.setAttribute('y', 30);
        textElement.setAttribute('text-anchor', 'middle');
        textElement.textContent = text;


        this.element.appendChild(textElement);

        const bbox = textElement.getBBox();

        const margins = {
            top: 5,
            right: 15,
            bottom: 5,
            left: 15
        }

        this.updateWidth(bbox.width + margins.left + margins.right + 5);
        textElement.setAttribute('x', this.width / 2);
        const rectXpos = this.width / 2 - bbox.width / 2 - margins.left;
        this.setRoundedRect(rectXpos, bbox.y - margins.top, bbox.width + margins.left + margins.right, bbox.height + margins.bottom + margins.top, 0);
        // this.setRoundedRect(rectXpos, bbox.y - margins.top, bbox.width + margins.left + margins.right, bbox.height + margins.bottom + margins.top, (bbox.height + margins.bottom + margins.top) / 8);
        this.element.insertBefore(this.element.lastChild, this.element.firstChild);
    }

    setRoundedRect(x, y, width, height, radius) {
        const rect = document.createElementNS('http://www.w3.org/2000/svg', 'rect');
        rect.setAttribute('x', x);
        rect.setAttribute('y', y);
        rect.setAttribute('width', width);
        rect.setAttribute('height', height);
        rect.setAttribute('rx', radius);
        rect.setAttribute('ry', radius);
        rect.setAttribute('fill', 'rgba(255,255,255,.0)');
        rect.setAttribute('stroke', 'white');

        this.element.appendChild(rect);
    }

    addClass(className) {
        this.element.classList.add(className);
    }

    hide() {
        this.element.style.display = 'none';
        this.element.style.opacity = 0;
    }

    show() {
        this.element.style.display = 'block';
        this.element.style.opacity = 1;
    }
}

class SVGGridImage {
    constructor(container, imagesFolder, imagesNumber = 7) {
        this.width = 400;
        this.imagesNumber = imagesNumber;
        this.currentImage = null;
        this.tl = gsap.timeline();
        this.loop = 0;
        this.imagesFolder = imagesFolder;
        this.isHovered = false;
        this.changing = false;
        this.element = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
        this.element.setAttribute('width', this.width);
        this.element.setAttribute('height', this.width);
        this.element.setAttribute('viewBox', '0 0 ' + this.width + ' ' + this.width);
        this.element.setAttribute('xmlns', 'http://www.w3.org/2000/svg');
        this.element.setAttribute('xmlns:xlink', 'http://www.w3.org/1999/xlink');
        this.element.setAttribute('class', 'svg-grid-image');

        container.appendChild(this.element);

        setTimeout(() => {
            this.loadImages(imagesFolder);
        }, Math.random() * 1000 + 500);

        this.element.addEventListener('click', () => {
            // if tl is paused, play it
            if (this.imagesNumber > 1) {
                if (this.tl.paused()) {
                    this.isHovered = false;
                    this.tl.play();
                    this.loadImages();
                }
            }
        });
        this.element.addEventListener('mouseleave', () => {
            this.isHovered = false;
        });
    }

    loadImages(oldIndex = null) {
        const imageIndex = Math.floor(Math.random() * this.imagesNumber) + 1;
        if (imageIndex === this.currentImage) {
            this.loadImages(oldIndex);
            return;
        }
        this.currentImage = imageIndex;
        const id = Math.random().toString(36).substring(7);
        const imagePath = `/images/${this.imagesFolder}/${imageIndex}.jpg`;
        this.splitImage(imagePath, id, imageIndex);
    }

    splitImage(imagePath, imageIndex, oldIndex) {
        const rows = 10;
        const cols = 10;
        const squareSize = this.width / rows;
        this.changing = true;

        this.element.style.transformStyle = 'preserve-3d';
        this.element.style.perspective = '1000px';
        this.element.style.transform = 'perspective(1000px)'

        for (let row = 0; row < rows; row++) {
            for (let col = 0; col < cols; col++) {
                const rect = document.createElementNS('http://www.w3.org/2000/svg', 'rect');
                rect.setAttribute('x', col * squareSize);
                rect.setAttribute('y', row * squareSize);
                rect.setAttribute('width', squareSize);
                rect.setAttribute('height', squareSize);
                rect.setAttribute('fill', `url(#image${row}-${col})`);
                rect.setAttribute('class', `grid-rect-${imageIndex}`);
                rect.style.transformStyle = 'preserve-3d';
                rect.style.backfaceVisibility = 'visible';
                rect.style.opacity = 0;
                this.element.prepend(rect);
            }
        }

        const image = new Image();
        image.src = imagePath;
        image.onload = () => {
            // Create defs section if it doesn't exist
            let defs = this.element.querySelector('defs');
            if (!defs) {
                defs = document.createElementNS('http://www.w3.org/2000/svg', 'defs');
                this.element.appendChild(defs);
            }

            const canvas = document.createElement('canvas');
            canvas.width = this.width;
            canvas.height = this.width;
            const ctx = canvas.getContext('2d');
            ctx.drawImage(image, 0, 0, this.width, this.width);

            for (let row = 0; row < rows; row++) {
                for (let col = 0; col < cols; col++) {
                    // Create temporary canvas
                    const tempCanvas = document.createElement('canvas');
                    tempCanvas.width = squareSize;
                    tempCanvas.height = squareSize;
                    const tempCtx = tempCanvas.getContext('2d');

                    // Draw image section
                    tempCtx.drawImage(
                        canvas,
                        col * squareSize, row * squareSize,
                        squareSize, squareSize,
                        0, 0,
                        squareSize, squareSize
                    );

                    // Create pattern
                    const patternId = `${imageIndex}-pattern-${row}-${col}`;
                    const pattern = document.createElementNS('http://www.w3.org/2000/svg', 'pattern');
                    pattern.setAttribute('id', patternId);
                    pattern.setAttribute('patternUnits', 'userSpaceOnUse');
                    pattern.setAttribute('width', squareSize);
                    pattern.setAttribute('height', squareSize);

                    // Create image element for pattern
                    const patternImage = document.createElementNS('http://www.w3.org/2000/svg', 'image');
                    patternImage.setAttribute('width', squareSize);
                    patternImage.setAttribute('height', squareSize);
                    patternImage.setAttribute('href', tempCanvas.toDataURL());

                    // Add image to pattern and pattern to defs
                    pattern.appendChild(patternImage);
                    defs.appendChild(pattern);

                    // Set rect fill to use pattern
                    const rect = this.element.querySelector(
                        `rect[x="${col * squareSize}"][y="${row * squareSize}"]`
                    );
                    rect.setAttribute('fill', `url(#${patternId})`);
                }
            }

            const rects = Array.from(this.element.querySelectorAll(`rect.grid-rect-${imageIndex}`));

            this.tl
                .to(rects, {
                    duration: 0,
                    // scale: 0,
                    [Math.random() > 0.5 ? 'x' : 'y']: "random(-200, 200)",
                })
                .to(rects, {
                    duration: 0.5,
                    opacity: 1,
                    transformOrigin: '50% 50%',
                    scale: 1,
                    x: 0,
                    y: 0,
                    ease: 'linear',
                    stagger: {
                        each: 0.01,
                        from: "random"
                    }, onComplete: () => {
                        if (!this.isHovered) {
                            this.tl.pause();
                        } else {
                            this.loadImages(oldIndex);
                        }
                    }
                })
                .to(rects, {
                    duration: 0.05,
                    opacity: 0,
                    // scale: 0,
                    [Math.random() > 0.5 ? 'x' : 'y']: "random(-200, 200)",
                    ease: 'linear',
                    stagger: {
                        each: 0.01,
                        from: "random"
                    }, onComplete: () => {
                        this.changing = false;
                        rects.forEach(rect => rect.remove());
                        for (let row = 0; row < rows; row++) {
                            for (let col = 0; col < cols; col++) {
                                const patternId = `${imageIndex}-pattern-${row}-${col}`;
                                const pattern = this.element.querySelector(`pattern#${patternId}`);
                                if (pattern)
                                    pattern.remove();
                            }
                        }
                    }
                });
        };
    }
}

const container = document.createElement('div');
container.setAttribute('id', 'content');
document.body.appendChild(container);

const pageContainer = document.createElement('div');
pageContainer.setAttribute('id', 'page');
document.body.appendChild(pageContainer);

const onClick = item => {
    // select all .svg-button elements
    const buttons = document.querySelectorAll('.svg-button');
    // fade out all buttons
    buttons.forEach(button => {
        button.style.opacity = 0;
    });

    setMoveLogo()

    history.pushState({}, '', item.history);
}

document.addEventListener('DOMContentLoaded', () => {
    const path = window.location.hash
        ? window.location.hash.slice(1)
        : window.location.pathname;

    // history.pushState(null, '', path);
    // window.location.location = path;
    if (!window.location.hash) {
        const menu = findMenu(path);
        updateMenu(menu);
    } else {
        history.pushState({}, '', path);
    }
});

window.addEventListener('popstate', (event) => {
    const path = window.location.pathname;
    const menu = findMenu(path);
    updateMenu(menu);
});

const originalPushState = history.pushState;
history.pushState = function (state, title, url) {
    originalPushState.apply(history, arguments);
    const menu = findMenu(url);
    updateMenu(menu);
};